@charset "utf-8";

.movie{
  width: 45%;
  background: #fff;
  padding: 20px;
  box-shadow: 0px 0px 10px #ccc;
  display: flex;
  margin-right: 30px;
  margin-bottom: 30px;
}
.movie img{
  max-width: 150px;
  width: 100%;
  position: relative;
  top: -30px;
  margin-right: 30px;
  box-shadow: 0px 20px 20px -12px #8f8f8f;
  transition: .3s;
  perspective: 200px;
}
.movie img:hover{
  transform: perspective(300px) rotateY(45deg);
}
.movie_data{

}
.movie_title .movie_year{
  font-weight: 300px;
}
.movie_title{
  font-size: 20px;
  margin-bottom: 5px;
  color: #2c2c2c;
}
.movie_genres{
  display: flex;
  flex-wrap: wrap;
  margin:  5px 0px;
}
.movie_genres_txt{
  margin-right: 10px;
  font-size: 14px;
}
/* year서식 */
.movie_year{
  margin-right: 10px;
  font-size: 14px;
  color: #000000;
}