@charset "utf-8";
ul,ol,li{
  list-style: none;
}
/* App.css */
body{
  font-family: '맑은고딕',Arial, Helvetica, sans-serif;
  font-size: 12px;
  color: #333;
  height: 100%;
  background-color: #eff3f7;
}
section{
  display: flex;
  flex-wrap: wrap;
}
/* pc버전 */
@media screen and (min-width:768px){
  .header{
    position: fixed;
    left: 0;
    top: 0;
    width: 180px;
    height: 100vh;
  }
  .header>h1{
    text-align: center;
    padding: 15px 0px;
  }
  main{
    position: relative;
    width: calc(100% - 180px);
    left: 180px; top: 0px;
    padding: 30px;
    box-sizing: border-box;
  }
  .load_img{
    position: fixed;
    top: 50%;left: 50%;
    transform: translate(-50%, -50%);
  }
  .movie_data li{
    position: relative;
    left: -40px;
  }
}
/* 모바일버전 */
@media screen and (max-width:767px){
  .header{
    position: relative;
    width: 100%;
    top: 0;
    height: 80px;
    background: #fff;
    line-height: 70px;
    border-bottom: 1px solid #000;
  }
  .header>h1{
    text-align: center;
  }
  .load_img{
    position: fixed;
    top: 50%;left: 50%;
    transform: translate(-50%, -50%);
  }
}